html {
  width: 100vw;
}

body {
  margin: 0;
  padding: 0;
  min-width: fit-content;
  background-color: black;
  overflow-x: auto;
  padding: 5%;
}

.App {
  text-align: center;
}

h1 {
  color: white;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15vh;
}

.App > span {
  color: white;
}

.waldo {
  border: 5px solid white;
  position: relative;
  width: fit-content;
  height: 1000px;
  margin: 0 auto;
}

.waldoImage {
  height: 100%;
  display: block;
}

.target {
  background-image: url("./images/target.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50%;
  position: absolute;
}

.hidden {
  display: none;
}

.hit {
  background-image: url("./images/hit.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50%;
  position: absolute;
  filter: drop-shadow(0 0 4px black);
}

.charList {
  position: absolute;
  background: rgb(0, 0, 0, 0.7);
  font-size: 1.4rem;
  color: white;
  border-radius: 1em;
  list-style: none;
  padding: 1%;
  display: grid;
  grid-auto-rows: 1fr;
}

.character {
  display: flex;
  align-items: center;
  border: 1px solid white;
  width: fit-content;
  border-radius: 1em;
  cursor: pointer;
  margin: 5% 0;
}

.character span {
  margin-right: 15px;
}

.character img {
  max-height: 80px;
}

.screenBlock {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.modal {
  background: white;
  width: 25%;
  height: fit-content;
  border-radius: 1rem;
  padding: 1%;
}

.modal h2 {
  text-align: center;
}

table {
  width: 100%;
}

th,
tr:nth-child(2n) {
  background-color: rgb(255, 106, 106);
}

.modal h3 {
  margin: 1% 0;
}

.score {
  width: 100%;
  display: flex;
  align-items: flex-end;
  gap: 5%;
  margin-bottom: 3%;
}

.addScore {
  padding: 2%;
  background-color: white;
  border: 2px solid rgb(255, 106, 106);
  border-radius: 1rem;
  position: relative;
  top: 8px;
  cursor: pointer;
}

.addScore:hover {
  border: 2px solid white;
  background-color: rgb(255, 106, 106);
  color: white;
}

.addScore:disabled:hover {
  border: 2px solid rgb(255, 106, 106);
  background-color: white;
  color: lightgray;
}

.addScore:disabled::after {
  content: "âœ“";
  position: absolute;
  color: green;
  font-size: 1.4rem;
  right: -30%;
  top: 50%;
  transform: translateY(-50%);
}

.inputGroup {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.time {
  display: flex;

  flex-direction: column;
}
